import "./src/Styles/tailwind.css";

function createFrame(queryString, authUrl) {
  var iframe = document.createElement("iframe");
  iframe.style.display = "none";
  document.body.appendChild(iframe);

  if (!authUrl) {
    const scriptName = "client.js";
    var scripts = Array.from(document.getElementsByTagName("script"));
    var script = scripts.find(
      (s) => s.src.includes("/" + scriptName) && new URL(s.src).pathname.endsWith(scriptName)
    );
    authUrl = script.src.substr(0, script.src.lastIndexOf("/") + 1);
  }
  iframe.src = authUrl + "frame.html?" + queryString;
}

window.Logout = function (authUrl) {
  createFrame("logout", authUrl);
};

window.RefreshToken = function (authUrl) {
  createFrame("refreshToken", authUrl);
};
